.ui-to-top {
    position: fixed;
    right: 15px;
    bottom: 15px;
    width: 58px;
    height: 58px;
    border-radius: 100%;
    font-size: 24px;
    line-height: 58px;
    overflow: hidden;
    text-align: center;
    text-decoration: none;
    z-index: 20;
    transition: .3s all ease;

    transform: translateY(100px);

    &::before {
        content: '\e941';
        font-family: 'Linearicons';
    }

    &,
    &:active,
    &:focus {
        color: $white;
        background: darken($primary, 6%);
    }

    &:hover {
        color: $white;
        background: $black;
    }

    &:focus {
        outline: 0;
    }

    &.active{
        transform: translateY(0);
        line-height: unset;
    }
}

.mobile .ui-to-top,
.tablet .ui-to-top {
    display: none !important;
}

@include media-breakpoint-up(sm) {
    .ui-to-top {
        right: 40px;
        bottom: 40px;
    }
}


